/* 1-й уровень */
ol {
  margin-top: 8px;
  list-style: none;
  counter-reset: num;
  /* 2-й уровень */
}
ol li {
  line-height: 160%;
  position: relative;
}
ol li::before {
  position: absolute;
  top: 0;
  left: 0;
}
ol > li {
  margin: 0 0 24px 0;
  padding-left: 32px;
  font-family: "Raleway", sans-serif;
  counter-increment: num;
}
ol > li::before {
  content: counter(num);
}
ol ol {
  counter-reset: num2;
  /* 3-й уровень */
}
ol ol > li {
  margin: 0 0 8px 0;
  padding-left: 42px;
  font-family: "Raleway", sans-serif;
  counter-increment: num2;
}
ol ol > li::before {
  content: counter(num) "." counter(num2);
}
ol ol ol {
  counter-reset: num3;
}
ol ol ol > li {
  margin: 0 0 8px 0;
  padding-left: 50px;
  font-family: "Raleway", sans-serif;
  counter-increment: num3;
}
ol ol ol > li::before {
  content: counter(num) "." counter(num2) "." counter(num3);
}
@media screen and (max-width: 920px) {
  ol ol li {
    padding-left: 32px !important;
    margin-left: -32px !important;
  }
}

