[data-animate] {
  transition: transform 0.6s 0s, opacity 0.6s;
  opacity: 0;
}

[data-animate-direction=bottom] {
  transform: translateY(50px);
}

[data-animate-direction=right] {
  transform: translateX(50px);
}

[data-animate-direction=left] {
  transform: translateX(-50px);
}

[data-animate-direction=top] {
  transform: translateY(-50px);
}

[data-animate-direction=right_strong] {
  transform: translateX(100%);
}

[data-animate-direction=left_strong] {
  transform: translateX(-100%);
}

[data-animate=true] {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

.fade-enter-active, .fade-leave-active {
  transition: opacity 0.3s;
}

.fade-enter, .fade-leave-to {
  opacity: 0;
}

.tabs-enter-active, .tabs-leave-active {
  transition: opacity 1s;
}

.tabs-enter, .tabs-leave-to {
  opacity: 0;
}

.page-enter-active {
  transition: transform 0.8s, opacity 0.6s;
  transition-delay: 0s;
  position: fixed !important;
  left: 0 !important;
  top: 0 !important;
  transform: translateX(100%);
  opacity: 0;
  z-index: 0;
}

.page-enter-to {
  transform: translateX(0);
  background-attachment: scroll !important;
  height: 100vh;
  overflow: hidden;
  opacity: 1;
  z-index: 2;
}

.page-leave-active {
  transition: transform 0.8s, opacity 0.5s;
}

.page-leave-to {
  transform: translateX(-100%);
  opacity: 0;
}

.fadeRight-enter-active, .fadeLeft-enter-active {
  transition: transform 0.6s, opacity 0.6s;
  transform: translateX(0%);
}

.fadeRight-enter, .fadeLeft-leave-to {
  transform: translateX(100%);
  opacity: 0;
}

.fadeRight-leave-active, .fadeLeft-leave-active {
  transition: transform 0.6s, opacity 0.6s;
  position: absolute;
  top: 0;
  width: 100%;
}

.fadeRight-leave-to, .fadeLeft-enter {
  transform: translateX(-100%);
  opacity: 0;
}

.fadeChange-enter-active {
  transition: opacity 0.4s 0.15s;
}

.fadeChange-leave-active {
  position: absolute;
  width: 100%;
  transition: opacity 0.4s;
}

.fadeChange-enter, .fadeChange-leave-to {
  opacity: 0;
}

.fadeHeight-enter-active {
  transition: max-height 1.5s, opacity 0.6s;
  max-height: 2000px;
  overflow: hidden;
}

.fadeHeight-leave-active {
  transition: max-height 0.6s, opacity 0.6s;
  max-height: 2000px;
  overflow: hidden;
}

.fadeHeight-enter,
.fadeHeight-leave-to {
  opacity: 0;
  max-height: 0;
  overflow: hidden;
}

.fadeTop-enter, .fadeTop-leave-to {
  transform: translateY(-100px);
  opacity: 0;
}

.fadeTop-enter-active {
