header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 12;
}

@keyframes header {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes headerOn {
  0% {
    opacity: 0;
    transform: translateY(-6%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes headerOff {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(-6%);
  }
}
